.Teacher {
  width: 100%;
}

.Teacher__header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  font-size: 0.8rem;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: stretch;
      align-items: stretch;
}

.Teacher__code-text {
  margin-top: 10px;
}

.Teacher__code {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid var(--black);
  border-radius: 4px;
  font-size: 1rem;
}

.Teacher__number-text {
  margin-top: 10px;
}

.Teacher__number-students {
  margin: 10px 0;
  padding: 10px;
  border: 1px solid var(--black);
  border-radius: 4px;
  font-size: 1rem;
  margin-bottom: 10px;
}

.Teacher__room {
  width: 100%;
  height: calc(100vh - 175px);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  padding: 10px 0;
  margin-bottom: 10px;
}

.Teacher__message {
  text-align: left;
  border: 1px solid var(--black);
  padding: 10px 20px;
  width: 100%;
  margin: 5px 0;
  font-size: 0.8rem;
  overflow-wrap: break-word;
}

.Teacher__exit {
  background: var(--white);
  border: 1px solid var(--gray);
  border-radius: 4px;
  padding: 10px;
  outline: none;
  color: var(--gray);
  -webkit-transition: all 100ms cubic-bezier(0.4, 0, 0.2, 1);
  -o-transition: all 100ms cubic-bezier(0.4, 0, 0.2, 1);
  transition: all 100ms cubic-bezier(0.4, 0, 0.2, 1);
  margin-top: 10px;
  letter-spacing: 2px;
  text-transform: uppercase;
  font-size: 0.7rem;
  cursor: pointer;
}

.Teacher__exit:hover,
.Teacher__exit:focus {
  border: 1px solid var(--red);
  background-color: var(--red);
  color: var(--white);
}

@media (min-width: 992px) {
  .Teacher__header {
    -ms-flex-direction: row;
        flex-direction: row;
    font-size: 0.8rem;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
  }

  .Teacher__code-text {
    margin-top: 0;
  }

  .Teacher__code {
    margin: 10px 0;
    padding: 10px;
    border: 1px solid var(--black);
    border-radius: 4px;
    font-size: 1rem;
  }

  .Teacher__number-text {
    margin-top: 0;
  }

  .Teacher__number_students {
    margin-bottom: 0;
  }

  .Teacher__exit {
    margin: 10px 0;
    -ms-flex-item-align: stretch;
        align-self: stretch;
    padding: 0 20px;
  }
}



