.Home {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: calc(90vh - 75px);
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

.Home__link {
  width: 100%;
  margin: 10px;
  padding: 10px;
  border: 1px solid var(--black);
  border-radius: 3px;
  text-decoration: none;
  color: var(--black);
}

@media (min-width: 576px) {
  .Home__link {
    width: 50%;
  }
}



